module.exports = " <div id=create-mc-question> <mdl-button primary raised class=float-button style=\"bottom: 80px\" @click=\"questionInbox.show = true\" v-bind:disabled=publishButton.disabled> <i class=material-icons>inbox</i> {{questionInbox.questions.length}} </mdl-button> <mdl-button primary raised class=float-button @click=publishQuestion() v-bind:disabled=publishButton.disabled>  </mdl-button> <div class=body_wrapper> <card> <div slot=content style=\"padding: 16px 16px 0 16px\"> <div class=flex-column> <div class=flex-row> <div class=\"flex-column flex-50\"> <span class=field-title></span> <select v-model=newQuestion.subject> <option v-for=\"subject in subjects\" v-bind:value=subject.id> {{ subject.name }} </option> </select> </div> <div class=\"set_difficulty flex-column flex-50\"> <span class=field-title style=margin-bottom:4px></span> <span class=flex-row> <i v-for=\"n in 5\" class=material-icons @click=\"newQuestion.difficulty = $index + 1\" :class=\"{'difficulty_status': newQuestion.difficulty > $index}\">star_rate</i> </span> </div> </div> <div class=flex-row style=\"padding-top: 25px\"> <div class=\"flex-column flex-50\"> <div style=\"margin-right: 10px\"> <span class=field-title></span> </div> <div class=\"flex-row flex-center flex-wrap\"> <span class=q-tag @click=removeTag($index) v-for=\"tag in newQuestion.tags\" track-by=$index>{{tag}}</span> </div> <div style=\"position: relative;top: -12px\"> <mdl-textfield label=. @keyup.enter=addTag() :value.sync=tag style=width:200px></mdl-textfield> </div> </div> <div class=\"flex-column flex-50\"> <span class=field-title></span> <select v-model=newQuestion.language v-on:change=setUserLanguage(newQuestion.language)> <option v-for=\"language in languages\" v-bind:value=language.id> {{ language.name }} </option> </select> </div> </div> </div> </div> </card> <div class=flex-column style=\"margin-bottom: 16px\"> <h5 style=margin-left:4px></h5> <div style=background:#ddd class=flex-column> <mdl-button primary @click=\"editorPreview.image.show = !editorPreview.image.show\"><i class=material-icons>photo</i> <span></span> </mdl-button> </div> <div v-show=editorPreview.image.show> <form v-on:change=readImg($event)> <input type=file id=uploadedImg /> </form> <div class=\"flex-column flex-center\"> <canvas v-el:fabricprocess></canvas> <mdl-textfield label= :value.sync=editorPreview.image.label></mdl-textfield> <mdl-button primary @click=outputImg()><i class=material-icons>photo</i> <span></span></mdl-button> </div> </div> </div> <div class=flex-column> <card> <div slot=content> <quill :toolbar=\"['italic', 'underline', { 'list': 'ordered'}, { 'list': 'bullet' }]\" :content.sync=editorPreview.question></quill> </div> </card> </div> <h5 style=margin-left:4px></h5> <div id=mc_input_container> <div class=flex-row> <div class=\"flex-column flex-50\"> <div class=mc_select :class=\"{'hightlight-answer': newQuestion.answer.mc === 0}\" @click=\"newQuestion.answer.mc = 0\">A</div> <card> <div slot=content> <quill :toolbar=\"['italic', 'underline', { 'list': 'ordered'}, { 'list': 'bullet' }]\" :content.sync=editorPreview.answer.mc[0]></quill> </div> </card> </div> <div class=\"flex-column flex-50\"> <div class=mc_select :class=\"{'hightlight-answer': newQuestion.answer.mc === 1}\" @click=\"newQuestion.answer.mc = 1\">B</div> <card> <div slot=content> <quill :toolbar=\"['italic', 'underline', { 'list': 'ordered'}, { 'list': 'bullet' }]\" :content.sync=editorPreview.answer.mc[1]></quill> </div> </card> </div> </div> <div class=flex-row> <div class=\"flex-column flex-50\"> <div class=mc_select :class=\"{'hightlight-answer': newQuestion.answer.mc === 2}\" @click=\"newQuestion.answer.mc = 2\">C</div> <card> <div slot=content> <quill :toolbar=\"['italic', 'underline', { 'list': 'ordered'}, { 'list': 'bullet' }]\" :content.sync=editorPreview.answer.mc[2]></quill> </div> </card> </div> <div class=\"flex-column flex-50\"> <div class=mc_select :class=\"{'hightlight-answer': newQuestion.answer.mc === 3}\